import { Container } from "react-bootstrap";
import Row from "react-bootstrap/Row";

import Icon from "../Shared/Icon/Icon";
import VideoCardItem from "./VideoCardItem";

import "./VideoCardStyle.scss";

interface ICardCardItemsProps {
  id: number | string,
  title: string,
  image: string,
  date: string,
  tutor: {
    name: string,
    avatar: string
  },
  rate: number
};

interface IVideoCardProps {
  title: string;
  cardItems: ICardCardItemsProps[],
}

const VideoCardContainer = ({
  title,
  cardItems
}: IVideoCardProps) => {
  return (
    <div className="video-card position-relative ">
      <h1 className="text-white bg-primary fw-lighter video-card__title">
        <Icon iconName='logoShape' variant="secondary" className="position-absolute video-card__title__icon" />
        {title}
      </h1>
      <Container fluid className="video-card__items">
        <Row>
          {cardItems.map((item) => (
            <VideoCardItem {...item} key={item.id} />
          ))}
        </Row>
      </Container>
    </div>
  )
}

export default VideoCardContainer;