import { ReactNode } from 'react';

import Container from 'react-bootstrap/Container';

import BreadCrumbs from '../../Shared/BreadCrumb/BreadCrumb';
import NavOne from '../../NavOne';
import AppNavbar from '../../Shared/AppNavbar/AppNavbar';

interface IHeaderProps {
  title?: string,
  children?: ReactNode,
}

const Header = ({ title, children }: IHeaderProps) => {

  const breadCrumbs = ["HeaderTwo.main", 'HeaderTwo.seminars'];

  return (
    <div className='bg-blue-with-circle'>
      <NavOne />
      <AppNavbar />
      <Container fluid className='mt-4'>
        <BreadCrumbs breadCrumbs={breadCrumbs} title={title} />
      </Container>
      {children}
    </div>
  );
}

export default Header
