import { useTranslation } from 'react-i18next';
import { Link } from 'react-router-dom';

import { Card, Button, Image } from 'react-bootstrap';

import { DefaultMomentFormat } from '../../../core/Mixins/MomentFormats';

import Icon from '../../Shared/Icon/Icon';

import styles from './styles.module.scss';

interface ICardItemsProps {
  image: string;
  title: string;
  date: string;
  tutor: { name: string; avatar: string };
}



const CardItems = ({
  image,
  title,
  date,
  tutor,
}: ICardItemsProps) => {
  const { t } = useTranslation();
  return (
    <Card className="mb-5 border-0 mx-auto px-5">
      <Card.Img variant="top" src={image} alt={`${title}-img`} height={200} />
      <Card.Body>
        <Card.Title className={styles.date}>
          <Icon iconName='calendar' className='me-3' iconSize={28} />
          <p className='text-secondary'> {DefaultMomentFormat(date)} </p>
        </Card.Title>
        <Card.Text className="h5 mb-3 text-blue"> {t(`${title}`)} </Card.Text>
        <Card.Text className={styles.tutor}>
          <Image src={tutor.avatar} alt={tutor.name} />
          {t(`${tutor.name}`)}
        </Card.Text>
      </Card.Body>
      <Card.Body className="p-0">

        <Link to="upcomming-webinars">
          <Button variant="primary" className={`py-3 ${styles.book}`}>
            {t('UpcomingWibList.book')}
          </Button>

        </Link>

      </Card.Body>
    </Card>
  );
}

export default CardItems;
