import UpcomingWibCard from './CardItems/CardItems';

import Carousel from 'react-multi-carousel';
import { Container } from 'react-bootstrap';
import Icon from '../Shared/Icon/Icon';
import { useTranslation } from 'react-i18next';

import 'react-multi-carousel/lib/styles.css';
import  './SliderCardStyles.scss';

interface IcardItemsProps {
  id: number,
  title: string,
  image: string,
  date: string,
  tutor: {
    name: string,
    avatar: string
  }
};

interface ISliderCardList {
  title: string,
  cardItems: IcardItemsProps[],
}


const SliderCardList = ({ title, cardItems }: ISliderCardList) => {
  const { i18n } = useTranslation();

  const responsive = {
    desktop: {
      breakpoint: { max: 3000, min: 1024 },
      items: 4,
      slidesToSlide: 4, // optional, default to 1.
    },
    tablet: {
      breakpoint: { max: 1024, min: 464 },
      items: 2,
      slidesToSlide: 2, // optional, default to 1.
    },
    mobile: {
      breakpoint: { max: 464, min: 0 },
      items: 1,
      slidesToSlide: 1, // optional, default to 1.
    },
  };

  return (
    <Container fluid className='pb-5 carousel'>
      <h1 className={`text-blue title`}>
        <span className='position-relative'>
          <Icon iconName='logoShape' variant='primary' iconSize={186} className={`position-absolute icon`} />
          {title}
        </span>
      </h1>
      <Carousel
        swipeable={false}
        draggable={true}
        showDots={false}
        responsive={responsive}
        autoPlay={false}
        keyBoardControl={true}
        customTransition="all .5"
        transitionDuration={500}
        containerClass="carousel-container"
        removeArrowOnDeviceType={['tablet', 'mobile']}
        dotListClass="custom-dot-list-style"
        rtl={i18n.language === "ar"}
        itemClass="carousel-item-padding-40-px"
      >
        {cardItems.map((item) => (
          <UpcomingWibCard {...item} key={item.id} />
        ))}
      </Carousel>

    </Container>
  );
}

export default SliderCardList;
