import Card from "react-bootstrap/Card";
import Image from "react-bootstrap/Image";
import Col from "react-bootstrap/Col";
import { DefaultMomentFormat } from "../../core/Mixins/MomentFormats";
import Rating from "../Shared/Rating/Rating";
import Icon from "../Shared/Icon/Icon";
import { Link } from "react-router-dom";

interface IVideoCardProps {
  image: string;
  title: string;
  date: string;
  tutor: { name: string; avatar: string },
  rate: number,
  id: number | string,
}

const VideoCardItem = ({
  image,
  title,
  date,
  tutor,
  rate,
  id
}: IVideoCardProps) => {

  return (
    <Col  lg={6} xl={4}>
      <Card className="mb-4 border-0 bg-white rounded-3 shadow">
        <Card.Img variant="top" src={image} alt={`${title}-img`} height={271} />
        <Card.Body>
          <Card.Title className="d-flex justify-content-between align-items-center mb-3">
            <small className='text-primary opacity-50'> {DefaultMomentFormat(date)}  </small>
            <Rating defaultValue={rate} readOnly />
          </Card.Title>
          <Card.Text className="h5 text-blue mb-4">
            <Link to={`/webinars/${id}`}>
              {title}
            </Link>
             </Card.Text>
          <div className="d-flex justify-content-between align-items-center">
            <Card.Text>
              <Image src={tutor.avatar} alt={`${tutor.name}-img`} className="me-2" />
              {tutor.name}
            </Card.Text>
            <Icon iconName="zoom" iconSize={107} height={24}  />
          </div>
        </Card.Body>
      </Card>
    </Col>
  )
}

export default VideoCardItem;