import { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";

import AdvBox from "../../components/AdvBox/AdvBox";
import Footer from "../../components/Layout/Footer/Footer";
import Header from "../../components/Layout/Header/Header";
import SliderCardList from "../../components/SliderCardList/SliderCardList";
import VideoCardContainer from "../../components/VideoCard/VideoCardContainer";
import WorkshopsData from "../../core/Data/Workshops";

const Workshops = () => {
  const { t } = useTranslation("workshops");

  const [courses, setCourses] = useState<any>([])

  useEffect(() => {
    try {
      const res = WorkshopsData;
      setCourses(res.courses)
    } catch (e) {
      console.log(e)
    }
  }, []);

  return (
    <>
      <Header title={t("courses")} />

      <AdvBox title={t("courses")} />

      <SliderCardList cardItems={courses.filter((course: any) => course.rate > 4)} title={t('interactiveCourses')} />

      <VideoCardContainer cardItems={courses} title={t('courses')} />

      <Footer />
    </>
  )
}

export default Workshops;