import Container from 'react-bootstrap/Container';

import AdvUnit from '../AdvUnit/AdvUnit';

import styles from './styles.module.scss';

interface IAdvBox {
  title: string,
}

export default function AdvBox({title}: IAdvBox) {
  const advBox = [
    {
      id: 1,
      title: title,
      heading: 'AdvBox.card1.description',
      iconName: 'check'
    },
    {
      id: 2,
      title: title,
      heading: 'AdvBox.card2.description',
      iconName: 'check'
    },
    {
      id: 3,
      title: title,
      heading: 'AdvBox.card3.description',
      iconName: 'check'
    },
  ];
  return (
    <Container fluid>
      <div className={`d-md-flex text-center text-md-start bg-white pt-5 pb-4 rounded justify-content-evenly position-relative z-index-10 ${styles.box}`}>
        {advBox.map((advUnit) => (
          <AdvUnit {...advUnit} key={advUnit.id} />
        ))}
      </div>
    </Container>
  );
}
